@import url('https://fonts.googleapis.com/css2?family=Inria+Sans:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Oswald:wght@200..700&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Oswald';
  font-weight: 400;
  box-sizing: border-box;
}

.comming_soon{
  color: #E8199B;
}



/*
BG color => #121923
Sub color => #222A37
Pink color => #E8199B
White color => white
*/
.App{
  background-color: #121923;
  width: 100%;
  height: 100vh;
  padding: 20px;
}

/* HEADER */

.coins{
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.coins__img{
  width: 30px;
  height: 30px;
  background: url('img/coin.svg') no-repeat center top / cover;
  margin-right: 5px;
}

.coins__score{
  color: #E8199B;
  font-size: 25px;
}

/* MENU */

.menu ul {
  width: calc(100% - 40px);
  height: 40px;
  background-color: #222A37;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.menu ul li{
  display: inline-block;
}

.menu ul li p{
  background-color: transparent;
  border: none;
  font-size: 10px;
}

.image_1, .image_2, .image_3, .image_4 { 
  width: auto;
  height: 20px;
  margin-top: 3px;
}

.image_1{ background: url('./img/Factory.svg') no-repeat center top / cover; }
.image_2{ background: url('./img/Friends.svg') no-repeat center top / cover; }
.image_3{ background: url('./img/Workers.svg') no-repeat center top / cover; }
.image_4{ background: url('./img/Tasks.svg') no-repeat center top / cover; }

.active{
  filter: invert(43%) sepia(83%) saturate(7396%) hue-rotate(306deg) brightness(91%) contrast(101%);
}

.no_active{
  filter: invert(9%) sepia(7%) saturate(2866%) hue-rotate(176deg) brightness(90%) contrast(96%);
}

/* INFO */
.info{
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.info__block{
  padding: 5px 25px;
  background-color: #222A37;
  border-radius: 5px;
  color: white;
  font-size: 10px;
}

.info__block p:last-child{
  color: #E8199B;
  text-align: center;
}

/* FARM BUTTON */

.farm_button{
  width: 60%;
  height: 210px;
  background-color: #222A37;
  margin: 0 auto;
  border-radius: 10px;
  margin-top:  35%;
  text-align: center;
  color: #E8199B;
  transition: .15s;
}

.farm_button__img{
  width: 100%;
  height: 70%;
  background: url('./img/btn.svg') no-repeat center top / cover;
  filter: invert(9%) sepia(7%) saturate(2866%) hue-rotate(176deg) brightness(90%) contrast(96%);
}

.farm_button:active{
  transform: scale(0.9);
}